@import url(https://fonts.google.com/specimen/Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* $minWidth: 320px; */

.grid-container {
  max-width: 1440px;
  display: grid;
  grid-template-areas: "header" "content";
  grid-template-columns: 1fr;
  margin: 0 auto;
  font-family: "Open+Sans";
  }

.header {
  height: 80px;
  grid-area: header;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 0.1rem #6290c5 solid;
}

.checkList {
  display: -webkit-flex;
  display: flex;
  float: left;
  width: 270px;
  border-right: 0.1rem #6d5c5c solid;
}

.content{
  grid-area: content;
  display: -webkit-flex;
  display: flex;
  /* background-color: rgb(218, 245, 240); */
  direction: ltr;
  direction: initial;
  min-height: 600px;
}

.main {
  display: -webkit-flex;
  display: flex;
  /* background-color: rgb(223, 160, 160); */
  float: left;
}



.stepContainer {
        -webkit-flex: 1 1;
                flex: 1 1;
}

.pointContainer {
    --primary-color: #2f80ed;
    --secondary-color: #e0e0e0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.point {
        min-width: 36px;
        width: 36px;
        height: 36px;
        border-radius: 50%;
        /* background-color: #2f80ed; */
        background-color: var(--secondary-color);
        border: thick double var(--secondary-color);
    }

.pre {
        height: 1px;
        width: 100%;
        margin-top: 18px;
        background-color: var(--secondary-color);
    }
.post {
        height: 1px;
        width: 100%;
        margin-top: 18px;
        background-color: var(--secondary-color);
    }
.done {
        background-color: var(--primary-color);
        
}
.inactive {
        background-color: var(--secondary-color);    
}
.current{
        border: thick double var(--primary-color);
}
.hide {
        visibility: hidden;
   }
.stepLabel {
        height: 1rem;
        min-width: 260px;
        text-align: center;
    }

